.homepage {
  height: 100%;
  font-size: 24px;
}
.homepage .left-col {
  background-color: #222729;
  box-shadow: inset -0.25em 0 0.25em 0 rgba(0, 0, 0, 0.125);
}
.homepage .left-col .homepage-left .logo {
  display: flex;
  justify-content: space-between;
  margin: 2em 1.5em;
}
.homepage .left-col .homepage-left .logo .logo-image {
  width: 48px;
  height: 48px;
}
.homepage .left-col .homepage-left .logo .logo-title {
  text-align: right;
}
.homepage .left-col .homepage-left .logo .logo-title .logo-title-name {
  color: #fff;
  font-weight: 600;
  font-size: 1em;
  line-height: 1em;
}
.homepage .left-col .homepage-left .logo .logo-title .logo-title-des {
  font-size: 0.6em;
  color: rgba(255, 255, 255, 0.5);
  line-height: 1.25em;
  margin: 0.5em 0 0 0;
}
.homepage .left-col .homepage-left .homepage-nav .nav-text {
  display: block;
  padding: 10px 16px;
  color: rgba(255, 255, 255, 0.5);
  text-decoration: none;
  outline: 0;
  border: 0;
  transition: none;
}
.homepage .left-col .homepage-left .homepage-nav .nav-text-active {
  background: rgba(0, 0, 0, 0.15);
  box-shadow: inset 0 0 0.25em 0 rgba(0, 0, 0, 0.125);
  color: #fff;
}
.homepage .ant-layout-content .issues-item {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
