.login-form-wrap {
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background-color: #e6f7ff;
}
.login-form-wrap .login-form {
  width: 360px;
  padding: 16px;
  margin: 10px;
  box-shadow: 0 10px 20px 0px rgba(0, 0, 0, 0.05);
  border-radius: 4px;
  background-color: #fff;
}
.login-form-wrap .login-form .switch-btn {
  cursor: pointer;
  color: #1890ff;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
